import React, { Component } from 'react'
import acsses from '../style/assets/images/icon-access-anywhere.svg'
import security from '../style/assets/images/icon-security.svg'
import collab from '../style/assets/images/icon-collaboration.svg'
import file from '../style/assets/images/icon-any-file.svg'

export default class PageTwo extends Component {
    render() {
        return (
            <section className="page-two">
                <section className="page-two-container">
                    <img className="acsses" src={acsses} alt="" />
                    <p className="headline">Access your files, anywhere</p>
                    <pre className="desc">The ability to use a smartphone, tablet, or computer</pre>


                    <pre className="desc"> to access your account means your files follow you</pre>
                    <pre className="desc">everywhere.</pre>

                </section >

                <section className="page-two-container">

                    <img className="security" src={security} alt="" />
                    <p className="headline">Security you can trust</p>
                    <pre className="desc">2-factor authentication and user-controlled encryption are</pre>
                    <pre className="desc">just a couple of the security features we allow to help</pre>
                    <pre className="desc">secure your files.</pre>
                </section>

                <section className="page-two-container">
                    <img className="collab" src={collab} alt="" />
                    <p className="headline">Real-time collaboration</p>
                    <pre className="desc">Securely share files and folders with friends, family and</pre>
                    <pre className="desc">colleagues for live collaboration. No email attachments</pre>
                    <pre className="desc">required.</pre>



                </section>









                <section className="page-two-container">

                    <img className="file" src={file} alt="" />
                    <p className="headline">Store any type of file</p>

                    <pre className="desc">Whether you're sharing holidays photos or work</pre>
                    <pre className="desc">documents, Fylo has you covered allowing for all file</pre>
                    <pre className="desc">types to be securely stored and shared.</pre>
                </section>



            </section >
        )
    }
}
