import React, { Component } from 'react'
import productive from '../style/assets/images/illustration-stay-productive.png'
import arrow from '../style/assets/images/icon-arrow.svg'

export default class extends Component {
    render() {
        return (
            <section className="page-three">


                <section className="img-container">
                    <img className="page-three-img" src={productive} alt="" />
                </section>



                <section className="text-container">
                    <p className="headline">Stay productive,</p>
                    <p className="headline">wherever you are</p>


                    <p className="desc">Never let location be an issue when accessing your files. Fylo has you</p>
                    <p className="desc">covered for all of your file storage needs.</p>
                    <br />
                    <p className="desc">Securely share files and folders with friends, family and colleagues for live</p>
                    <p className="desc">collaboration. No email attachments required.</p>

                    <div className="arrow">
                        See how Fylo works <img src={arrow} alt="" />
                    </div>
                </section>



            </section >
        )
    }
}
