import React, { Component } from 'react'
import mainImg from '../style/assets/images/illustration-intro.png'
import curvey from '../style/assets/images/bg-curvy-desktop.svg'

export default class PageOne extends Component {
    render() {
        return (

            <section className="page-one">
                <img className="mainImg" src={mainImg} alt="" />
                <pre className="po-headline">All your files in one secure location,
                     </pre>
                <pre className="po-headline">accessible anywhere.</pre>
                <pre className="po-desc">Fylo stores all your most important files in one secure location. </pre>

                <pre className="po-desc">Access them wherever you need, share and collaborate with</pre>

                <pre className="po-desc"> friends family, and co-workers.</pre>

                <button>Get Started</button>

                <img className="back" src={curvey} alt="" />
            </section>


        )
    }
}
