import React, { Component } from 'react';
import { connect } from 'react-redux';

import { loadReviews, addReview } from '../actions/ReviewActions.js';
import { loadUsers } from '../actions/UserActions.js';
import { Link } from 'react-router-dom';
import PageOne from '../cmps/PageOne'
import PageTwo from '../cmps/PageTwo.jsx';
import PageThree from '../cmps/PageThree.jsx';
import PageFour from '../cmps/PageFour.jsx';
import PageFive from '../cmps/PageFive.jsx';

import logo from '../style/assets/images/logo.svg'

class Home extends Component {
  state = {
    reviewToEdit: {
      txt: '',
      aboutUserId: ''
    }
  };
  componentDidMount() {
    this.props.loadReviews();
    this.props.loadUsers();
  }

  handleChange = ev => {
    const { name, value } = ev.target;
    this.setState(prevState => ({
      reviewToEdit: {
        ...prevState.reviewToEdit,
        [name]: value
      }
    }));
  };

  addReview = ev => {
    ev.preventDefault();
    this.props.addReview(this.state.reviewToEdit);
    this.setState({ reviewToEdit: { txt: '', aboutUserId: '' } });
  };

  render() {
    return (
      <>
        <section className="main">
          <nav className="main-nav">
            <img src={logo}></img>
            <section className="nav-links">
              <p className="nav-link-text pointer">Features</p>
              <p className="nav-link-text pointer">Team</p>
              <p className="pointer">Sign in</p>
            </section>
          </nav>
          <PageOne />
          <PageTwo />
          <PageThree />
          <PageFour />
          <PageFive />
          <div className="page-five-bgc"></div>
          <div className="page-five-bgc-two"></div>
        </section>

      </>


    );
  }
}

const mapStateToProps = state => {
  return {
    reviews: state.review.reviews,
    users: state.user.users,
    loggedInUser: state.user.loggedInUser
  };
};
const mapDispatchToProps = {
  loadReviews,
  loadUsers,
  addReview
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
