import React, { Component } from 'react'

import location from '../style/assets/images/icon-location.svg'
import phone from '../style/assets/images/icon-phone.svg'
import email from '../style/assets/images/icon-email.svg'

import insta from '../style/assets/images/instagram.png'
import twitter from '../style/assets/images/twitter.png'
import facebook from '../style/assets/images/facebook.png'

import logo from '../style/assets/images/logo.svg'

export default class PageFive extends Component {
    render() {
        return (
            <>

                <section className="page-five">
                    <section className="page-five-col">
                        <div className="logo">
                            <img src={logo} alt="" />
                        </div>

                        <section className="info flex j-sb">
                            <section className="fylo-container">
                                <div className="location-desc">
                                    <img src={location} alt="" />
                                    <div className="desc-column">
                                        <p className="location-desc">Lorem ipsum dolor sit amet, consectetur</p>
                                        <p className="location-desc">adipiscing elit, sed do eiusmod tempor</p>
                                        <p className="location-desc">incididunt ut labore et dolore magna aliqua</p>
                                    </div>
                                </div>

                            </section>


                            <section className="contact-container">


                                <div className="phone flex">
                                    <img className="phoneImg" src={phone} alt="" />
                                    <p className="contact-text">+1-543-123-4567</p>
                                </div>

                                <div className="email flex">
                                    <img className="emailImg" src={email} alt="" />
                                    <p className="contact-text">example@fylo.com</p>
                                </div>

                            </section>


                            <section className="links-one-container">

                                <p className="link-one-text">About us</p>
                                <p className="link-one-text">Jobs</p>
                                <p className="link-one-text">Press</p>
                                <p className="link-one-text">Blog</p>
                            </section>


                            <section className="links-one-container">
                                <p className="link-one-text">Contact us</p>
                                <p className="link-one-text">Terms</p>
                                <p className="link-one-text">Privacy</p>

                            </section>


                            <section className="media-container">

                                <div className="img-container">
                                    <img src={facebook} alt="" />
                                </div>

                                <div className="img-container">
                                    <img src={twitter} alt="" />
                                </div>
                                <div className="img-container">

                                    <img src={insta} alt="" />
                                </div>
                            </section>


                        </section>
                    </section>






                </section>


            </>
        )
    }
}
